.main-nav {
  position: relative;
  width: auto;
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
  min-height: 70px;
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav__toggle {
  position: absolute;
  top: 6px;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 28px;
    right: 21px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--opened {
  background-color: #283645;
  transform: translateY(-130px);
  margin-bottom: -115px;
}
@media (min-width: 660px) {
  .main-nav--opened {
    transform: translateY(-130px);
  }
}
@media (min-width: 1200px) {
  .main-nav--opened {
    margin-bottom: 30px;
    background-color: transparent;
    transform: translateY(0);
  }
}
@media (min-width: 1200px) {
  .main-nav--closed {
    background-color: transparent;
  }
}
.main-nav--closed .main-nav__burger {
  display: block;
  position: absolute;
  top: 14px;
  right: 11px;
  width: 50px;
  height: 24px;
}
.main-nav--opened .main-nav__burger {
  display: none;
}
.main-nav--closed .main-nav__cross {
  display: none;
}
.main-nav--opened .main-nav__cross {
  display: block;
  position: absolute;
  top: 0;
  right: -14px;
  width: 50px;
  height: 24px;
}
.main-nav__burger path:hover {
  fill: #d22856;
}
.main-nav__cross path:hover {
  fill: #d22856;
}
.main-nav__burger path:active {
  opacity: 0.3;
}
.main-nav__cross path:active {
  opacity: 0.3;
}
.main-nav--closed .main__nav__toggle {
  content: "";
  position: absolute;
  top: 21px;
  right: 20px;
  width: 50px;
  height: 24px;
}
.main-nav--opened .main-nav__toggle {
  content: "";
  position: absolute;
  top: 22px;
  right: 21px;
  width: 23px;
  height: 23px;
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__toggle {
    top: 43px;
    right: 21px;
  }
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
